@font-face {
  font-family: "Calibre Bold";
  font-style: normal;
  src: local("Calibre Bold"), url("../fonts/141215082829Calibre-Bold.eot") format("embedded-opentype"), url("../fonts/141215082829Calibre-Bold.woff") format("woff");
}

@font-face {
  font-family: "Calibre Regular";
  font-style: normal;
  src: local("Calibre Regular"), url("../fonts/141215082804Calibre-Regular.eot") format("embedded-opentype"), url("../fonts/141215082804Calibre-Regular.woff") format("woff");
}

@font-face {
	font-family: 'Calibre Italic';
	src: url('../fonts/Calibre-RegularItalic.eot');
	src: url('../fonts/Calibre-RegularItalic.eot?#iefix') format('embedded-opentype'),
		url('../fonts/Calibre-RegularItalic.woff') format('woff'),
		url('../fonts/Calibre-RegularItalic.ttf') format('truetype');
	font-weight: normal;
	font-style: italic;
}




